@use 'base/variables' as v;
@use 'base/mixins' as m;
.inicio_noticias{
    background-color: #FDBD35;
    &__grid{
        padding: 4rem 0;
        @include m.tablet {
            @include m.grid(2,1rem);   
           }
    }
    &__img{
        width: 100%;   
    }
    &__texto{
    p{
        text-align: justify;
        margin: 0;
        color:  v.$blanco;
        font-weight: bold;
        font-size: 2.4rem;
    }
    }
}
.noticiero__inicio{
    margin: 2rem 0;
    &__titulo{
        text-align: left;
    }
    &__grid{
        @include m.tablet {
            @include m.grid(3,3rem);
        }
    }
}
.noticia{
    padding: 2rem;
    &__mask{
        overflow: hidden;
        width: 90%;
        margin: 0 auto;
        img{
            height: 25rem;
            transition: all .3s ease-in-out;
            &:hover{
                transform: scale(1.3);
            }
        }
    }
    &__titulo{
        color: #606060;
        margin: 1rem 0 0 0;
        text-align: center
    }
    &__btn{
        @include m.boton(v.$botones,v.$blanco);
    }
}